import React from 'react';
import './sortingvisualiser.css';
// import Motivator from '../motivator/motivator';
import Angularsorter from './angularsorter';

import {bubblesortalgorithm,selectionsortalgorithm,insertionsortalgorithm,quicksortalgorithm,heapsortalgorithm,} from  './sortingalgorithms.js';


class Sortingvisualiser extends React.Component {
constructor(props) {
    super(props);

    this.state = {
        array:[],
        algorithm:"quicksort",
        inuse:1,
    };
}
resetarray=()=>{
    const array=[];
    for(let i=0;i<=150;i++){
    array.push(Math.floor(Math.random()*(530-5+1)+5))
    }
    this.setState({array});
}
componentDidMount(){

    this.resetarray();
}
 
quicksort(){
const animation=quicksortalgorithm(this.state.array);
const bars=document.getElementsByClassName("bars");
for(let i=0;i<animation.length;i++){
    const [baroneidx,bartwoidx,isnotequal]=animation[i];
    const baronestyle=bars[baroneidx].style;
    const bartwostyle=bars[bartwoidx].style;
    if(isnotequal){
        
        setTimeout(()=>{
           
            baronestyle.backgroundColor="red";
            bartwostyle.backgroundcolor="red";
    
    
        const baronenewheight=bartwostyle.height;
        const bartwonewheight=baronestyle.height;
        baronestyle.height=baronenewheight;
            bartwostyle.height=bartwonewheight;
        setTimeout(()=>{
            baronestyle.backgroundColor="#304847";
            bartwostyle.backgroundColor="#304847";},i*3);
        

       
    },i*30);
   
    }
    else{

        setTimeout(()=>{
           
            baronestyle.backgroundColor="green";
            bartwostyle.backgroundcolor="green";
    
    
        const baronenewheight=bartwostyle.height;
        const bartwonewheight=baronestyle.height;
        baronestyle.height=baronenewheight;
            bartwostyle.height=bartwonewheight;
        setTimeout(()=>{
            baronestyle.backgroundColor="#304847";
            bartwostyle.backgroundColor="#304847";},i*3);
        

       
    },i*30);

    }
    

}


}
selectionsort(){
    const animation=selectionsortalgorithm(this.state.array);
    
    for(let i=0;i<animation.length;i++){
        const bars=document.getElementsByClassName("bars");
        const [baroneidx,bartwoidx,isswap]=animation[i];
        const baronestyle=bars[baroneidx].style;
        const bartwostyle=bars[bartwoidx].style;
        if(isswap){
            
            setTimeout(()=>{
           
                baronestyle.backgroundColor="red";
                bartwostyle.backgroundcolor="red";
        
        
            const baronenewheight=bartwostyle.height;
            const bartwonewheight=baronestyle.height;
            baronestyle.height=baronenewheight;
                bartwostyle.height=bartwonewheight;
            setTimeout(()=>{
                baronestyle.backgroundColor="#304847";
                bartwostyle.backgroundColor="#304847";},i*3);
            
    
           
        },i*30);
       

        }
        else{
            
            setTimeout(()=>{
            
                baronestyle.backgroundColor="green";
                bartwostyle.backgroundcolor="green"; 
                
            setTimeout(()=>{ baronestyle.backgroundColor="#304847";
            bartwostyle.backgroundColor="#304847";
                },i*3);
               
            },i*30);
            

        }
        
        


    }
   
    } 

    
insertionsort()

    {
        const animation=insertionsortalgorithm(this.state.array);
    
    for(let i=0;i<animation.length;i++){
            const bars=document.getElementsByClassName("bars");
            const [baroneidx,bartwoidx,isswap]=animation[i];
       
            if(isswap){
                const baronestyle=bars[baroneidx].style;
                setTimeout(()=>{
           
                    baronestyle.backgroundColor="green";
                
                
        
            
                    baronestyle.height=`${bartwoidx}px`;
                
                    setTimeout(()=>{
                            baronestyle.backgroundColor="#304847";
                                    },i*1);
            
    
           
                    },i*3);
       

            }
            else{
                    const baronestyle=bars[baroneidx].style;
                    const bartwostyle=bars[bartwoidx].style;
            
                    setTimeout(()=>{
           
                            baronestyle.backgroundColor="red";
                
        
        
                            const baronenewheight=bartwostyle.height;
            
                            baronestyle.height=baronenewheight;
            
                            setTimeout(()=>{
                                    baronestyle.backgroundColor="#304847";
                            },i*1);
            
    
           
                    },i*3);
                }
    
        }
    } 
  
    
heapsort(){
    const animation=heapsortalgorithm(this.state.array);
    const bars=document.getElementsByClassName("bars");
    for(let i=0;i<animation.length;i++){
        const [baroneidx,bartwoidx]=animation[i];
        const baronestyle=bars[baroneidx].style;
        const bartwostyle=bars[bartwoidx].style;
       
            
            setTimeout(()=>{
               
                baronestyle.backgroundColor="red";
                bartwostyle.backgroundcolor="red";
        
        
            const baronenewheight=bartwostyle.height;
            const bartwonewheight=baronestyle.height;
            baronestyle.height=baronenewheight;
                bartwostyle.height=bartwonewheight;
            setTimeout(()=>{
                baronestyle.backgroundColor="#304847";
                bartwostyle.backgroundColor="#304847";},i*3);
            
    
           
        },i*20);
       
        
       
    
    }




}



bubblesort(){
    const animation=bubblesortalgorithm(this.state.array);
    for(let i=0;i<animation.length;i++){
        const bars=document.getElementsByClassName("bars");
        const [baroneidx,bartwoidx,isswap]=animation[i];
        const baronestyle=bars[baroneidx].style;
        const bartwostyle=bars[bartwoidx].style;
        if(isswap){
            
            setTimeout(()=>{
           
                baronestyle.backgroundColor="red";
                bartwostyle.backgroundcolor="red";
        
        
            const baronenewheight=bartwostyle.height;
            const bartwonewheight=baronestyle.height;
            baronestyle.height=baronenewheight;
                bartwostyle.height=bartwonewheight;
            setTimeout(()=>{
                baronestyle.backgroundColor="#304847";
                bartwostyle.backgroundColor="#304847";},i*3);
            
    
           
        },i*1.5);
       

        }
        else{
            
            setTimeout(()=>{
            
                baronestyle.backgroundColor="green";
                bartwostyle.backgroundcolor="green"; 
                
            setTimeout(()=>{ baronestyle.backgroundColor="#304847";
            bartwostyle.backgroundColor="#304847";
                },i*3);
               
            },i*1.5);
            

        }
        


    }



}
changealgorithm=(event)=>{
this.setState({
    algorithm: event.target.value
})



}
visualise=(event)=>{
    const k=this.state.algorithm;
    if(k==="mergesort")this.mergesort();
    else if(k==="quicksort")this.quicksort();
    else if(k==="heapsort")this.heapsort();
    else if(k==="selectionsort")this.selectionsort();
    else if(k==="bubblesort")this.bubblesort();
    else if(k==="insertionsort")this.insertionsort();

    event.preventDefault();
}
    render() {
        const {array}=this.state;
        return (<div className="barsbox">
                
                <div >
                   {/* <button className="button"onClick={this.resetarray}>Generator</button> */}
                    <table className="navbarr" bgcolor="#404040"><tr className="navbarr" >
                    
                   
                        <td>
                        <form ><span className="lg-col-3  p-3 m-2 sm-col-12 p-3 m-4"><button className="btn btn-secondary generator" type="submit">Generator</button></span></form>
                        
                            <form  ><span className="lg-col-4 p-3 m-4 sm-col-5 m-3"> <label className="label" >Algorithm:</label>
                                <select value={this.state.algorithm} onChange={this.changealgorithm}> 
                                    {/* <option value="mergesort">Merge Sort</option> */}
                                    <option value="quicksort">Quick Sort</option>
                                    <option value="heapsort">Heap Sort</option>
                                    <option value="selectionsort">Selection Sort</option>
                                    <option value="bubblesort">Bubble Sort</option>
                                    <option value="insertionsort">Insertion Sort</option>

                                </select></span>
                                <span >
                                <button  className="btn btn-secondary lg-lg-col-4 p-2 m-3 sm-col-12 p-2 m-3"  onClick={this.visualise}>Sort</button>
                                </span>
                            

                        </form>

                                </td></tr></table>
                      
                            
                                                  
              
                        


                </div>
                <div className="sortarea">
                {
                   array.map((value,index)=>
                   <div className="bars" key={index} 
                   style={{height:`${value}px`}}>

                        {}

                   </div>)} 


                </div>
                <div>
                    <Angularsorter />
                </div>
                {/* <div><Motivator /></div> */}
               
                  
            </div>
           
        );
    }
}


// #endregion

export default Sortingvisualiser;