import React, { useState,useEffect } from 'react'
import './angularsorter.css'
import {bubblesortalgorithm,selectionsortalgorithm,quicksortalgorithm,heapsortalgorithm,insertionsortalgorithm} from './sortingalgorithms'



const Angularsorter = () => {
    const [angles_arr,set_angles_arr] = useState([]);
    const [sortable,setSortable] = useState(true);
    const [algorithm,setAlgorithm] = useState('quicksort');
    

    
const resetArray = () =>{
    let temp=[];
    let n = window.innerWidth<500?50:window.innerWidth<800?100:180;
    console.log(window.innerWidth);
    for(let i=0;i<n;i++){
        temp.push(Math.floor(Math.random()*180)+1)
       
    }
    set_angles_arr(temp);


    
    
}


const selectionsortAnimation =() =>{
    const temparr = [...angles_arr];
    const animation=selectionsortalgorithm(temparr);
    
    for(let i=0;i<animation.length;i++){
        const bars=document.getElementsByClassName("angledBars");
        const [baroneidx,bartwoidx,isswap]=animation[i];
        const baronestyle=bars[baroneidx].style;
        const bartwostyle=bars[bartwoidx].style;
        if(isswap){
            
            setTimeout(()=>{
           
                baronestyle.backgroundColor="red";
                bartwostyle.backgroundcolor="red";
        
        
            const baronenewheight=bartwostyle.transform;
            const bartwonewheight=baronestyle.transform;
            baronestyle.transform=baronenewheight;
                bartwostyle.transform=bartwonewheight;
            setTimeout(()=>{
                baronestyle.backgroundColor="black";
                bartwostyle.backgroundColor="black";},i*3);
            
    
           
        },i*30);
       

        }
        else{
            
            setTimeout(()=>{
            
                baronestyle.backgroundColor="green";
                bartwostyle.backgroundcolor="green"; 
                
            setTimeout(()=>{ baronestyle.backgroundColor="black";
            bartwostyle.backgroundColor="black";
                },i*3);
               
            },i*30);
            

        }
        
        


    }
   
    } 

    
const insertionsortAnimation =() =>

    {
        const temparr = [...angles_arr];
        const animation=insertionsortalgorithm(temparr);
    
    for(let i=0;i<animation.length;i++){
            const bars=document.getElementsByClassName("angledBars");
            const [baroneidx,bartwoidx,isswap]=animation[i];
       
            if(isswap){
                const baronestyle=bars[baroneidx].style;
                setTimeout(()=>{
           
                    baronestyle.backgroundColor="green";
                
                
        
            
                    baronestyle.transform=`rotate(${bartwoidx-90}deg)`;
                
                    setTimeout(()=>{
                            baronestyle.backgroundColor="black";
                                    },i*1);
            
    
           
                    },i*3);
       

            }
            else{
                    const baronestyle=bars[baroneidx].style;
                    const bartwostyle=bars[bartwoidx].style;
            
                    setTimeout(()=>{
           
                            baronestyle.backgroundColor="red";
                
        
        
                            const baronenewheight=bartwostyle.transform;
            
                            baronestyle.transform=baronenewheight;
            
                            setTimeout(()=>{
                                    baronestyle.backgroundColor="black";
                            },i*1);
            
    
           
                    },i*3);
                }
    
        }
    } 
  
    
const heapsortAnimation = () =>{
    const temparr = [...angles_arr];
    const animation=heapsortalgorithm(temparr);
    const bars=document.getElementsByClassName("angledBars");
    for(let i=0;i<animation.length;i++){
        const [baroneidx,bartwoidx]=animation[i];
        const baronestyle=bars[baroneidx].style;
        const bartwostyle=bars[bartwoidx].style;
       
            
            setTimeout(()=>{
               
                baronestyle.backgroundColor="red";
                bartwostyle.backgroundcolor="red";
        
        
            const baronenewheight=bartwostyle.transform;
            const bartwonewheight=baronestyle.transform;
            baronestyle.transform=baronenewheight;
                bartwostyle.transform=bartwonewheight;
            setTimeout(()=>{
                baronestyle.backgroundColor="black";
                bartwostyle.backgroundColor="black";},i*3);
            
    
           
        },i*20);
       
        
       
    
    }




}


const quicksortAnimation =() =>{
    const temparr = [...angles_arr];
    const animation=quicksortalgorithm(temparr);
    const bars=document.getElementsByClassName("angledBars");
    for(let i=0;i<animation.length;i++){
        const [baroneidx,bartwoidx,isnotequal]=animation[i];
        const baronestyle=bars[baroneidx].style;
        const bartwostyle=bars[bartwoidx].style;
        if(isnotequal){
            
            setTimeout(()=>{
               
                baronestyle.backgroundColor="red";
                bartwostyle.backgroundcolor="red";
        
        
            const baronenewheight=bartwostyle.transform;
            const bartwonewheight=baronestyle.transform;
            baronestyle.transform=baronenewheight;
                bartwostyle.transform=bartwonewheight;
            setTimeout(()=>{
                baronestyle.backgroundColor="black";
                bartwostyle.backgroundColor="black";},i*3);
            
    
           
        },i*30);
       
        }
        else{
    
            setTimeout(()=>{
               
                baronestyle.backgroundColor="green";
                bartwostyle.backgroundcolor="green";
        
        
            const baronenewheight=bartwostyle.transform;
            const bartwonewheight=baronestyle.transform;
            baronestyle.transform=baronenewheight;
                bartwostyle.transform=bartwonewheight;
            setTimeout(()=>{
                baronestyle.backgroundColor="black";
                bartwostyle.backgroundColor="black";},i*3);
            
    
           
        },i*30);
    
        }
        
    
    }
    
    
    }



     

const bubblesortAnimation = () =>{
    const temparr = [...angles_arr]
    const animations = bubblesortalgorithm(temparr);
    for(let i=0;i<animations.length;i++){
        const angledBars = document.getElementsByClassName('angledBars');
        const [abar1,abar2,isswap]= animations[i];
        const bar1style = angledBars[abar1].style;
        const bar2style = angledBars[abar2].style;

        if(isswap){
            
            setTimeout(()=>{
                bar1style.backgroundColor = 'red';
                bar2style.backgroundColor = 'red';
                let bar1rotate = bar1style.transform;
                let bar2rotate = bar2style.transform;

                bar1style.transform = bar2rotate;
                bar2style.transform = bar1rotate;

                setTimeout(()=>{
                    bar1style.backgroundColor="black";
                    bar2style.backgroundColor="black";},i*1);

                
               

            },i*2);
           
        }
        else{
            bar1style.backgroundColor = 'green';
            bar2style.backgroundColor = 'green';
            setTimeout(()=>{
                bar1style.backgroundColor="black";
                bar2style.backgroundColor="black";},i*1);

        }
    }

}
const sortAnimation =() =>{
    switch(algorithm){
        case 'bubblesort':{
            bubblesortAnimation();
            break;
        }
        case 'selectionsort':{
            selectionsortAnimation();
            break;
        }
        case 'insertionsort':{
            insertionsortAnimation();
            break;
        }
        case 'quicksort':{
            quicksortAnimation();
            break;
        }
        case 'heapsort':{
            heapsortAnimation();
            break;
        }

        default:{
            bubblesortAnimation();
        }

    }
}


    useEffect(()=>{
        resetArray();

    },[])

    return (
        <div>
            <div className='Navbar'>
                
                <button onClick={()=>{resetArray();setSortable(true)}}>Generator</button>
                <select value={algorithm} onChange = {(e)=>{setAlgorithm(e.target.value)}} disabled={!sortable}> 
                    <option value="quicksort">Quick Sort</option>
                                    <option value="heapsort">Heap Sort</option>
                                    <option value="selectionsort">Selection Sort</option>
                                    <option value="bubblesort">Bubble Sort</option>
                                    <option value="insertionsort">Insertion Sort</option></select>
                {
                <button  onClick={()=>{sortAnimation();setSortable(false)}} 
                disabled={!sortable} 
                >Sort</button>
                }
            </div>
            <div className = 'sortarena'>
           
           {
           angles_arr.map((val,idx)=>{
              return <div className="angledBars" key={idx}
               style = {{transform:`rotate(${val-90}deg) `,backgroundColor:'black'}}
               >{}

               </div>
           })
           }
       </div>
        </div>
        
    )
}

export default Angularsorter
