export function mergesortalgorithm(array){
    const animations=[];
    merge(array,animations);
    return animations;
 }; 
function merge(a,animations){
if(a.length>1){
    let mid=Math.floor(a.length/2);
    const l=[];
    const r=[];
    for(let q=0;q<mid;q++){
        l.push(a[q]);
    }
    for(let w=mid;w<a.length;w++){
        r.push(a[w]);
    }
    merge(l,animations);
    merge(r,animations);
    let i=0,j=0,k=0;
    while (i<l.length && j<r.length){
            if (l[i]<r[j]){
                animations.push([k,i]);
                a[k]=l[i];
                i=i+1;
            }
            else{
                animations.push([k,j]);
                a[k]=r[j];
                j=j+1;
            }
k=k+1;
    }
    while(i<l.length){
        animations.push([k,i]);
        a[k]=l[i];
        i=i+1;
        k=k+1;

    }
    while(i<r.length){
        animations.push([k,j]);
                a[k]=r[j];
                j=j+1;
                k=k+1;

    }



}


}
export function bubblesortalgorithm(array){
// const duparray=[];
const animation=[];

for(let i=0;i<array.length-1;i++){
    for(let j=0;j<array.length-i-1;j++){
        
        const animations=[j,j+1,0];
        if (array[j]>array[j+1]){
            animations[2]=1;
            let temp=array[j];
            array[j]=array[j+1];
            array[j+1]=temp;

        }
        animation.push(animations);
    }

}
return animation;



}
export function selectionsortalgorithm(array){
    
const animation=[];
for (let i=0;i<array.length;i++){
    let minidx=i;
for (let j=i+1;j<array.length;j++){

    if(array[minidx]>array[j])
   {
       minidx=j;
   } 


}
const animate=[i,minidx,0];
if (i!=minidx){
    animate[2]=1;
    let temp=array[i];
    array[i]=array[minidx];
    array[minidx]=temp;


}
animation.push(animate);
}
return animation


}
export function insertionsortalgorithm(array){
const animation=[];
for(let i=1;i<array.length;i++){
let key=array[i];
let j=i-1;
while(j>=0 && key<array[j]){
    array[j+1]=array[j];
    animation.push([j+1,j,0]);
    j=j-1;
   
}
array[j+1]=key;
if (j+1!=i){
 animation.push([j+1,key,1])   
}



}

return animation;



}
function partition(array,low,high){
    
    let animation=[];
    let i=low-1;
    let pivot=array[high];
    for(let j=low;j<high;j++){
        if(array[j]<=pivot){

            i=i+1;
            let k= (i!=j)?1:0;
            animation.push([i,j,k]);
            let temp=array[i];
            array[i]=array[j];
            array[j]=temp;
        }


    }
    let k=(i+1!=high)?1:0;
    animation.push([i+1,high,k])
    let temp=array[i+1];
    array[i+1]=array[high];
    array[high]=temp;
    let animate={
        animations:animation,
        index:i+1
    };
    return animate;


}
function quick(array,low,high,animations){

    if(low<high){
        let p=partition(array,low,high);
        for(let i=0;i<p.animations.length;i++){
            animations.push(p.animations[i]);
        }
        
        quick(array,low,p.index-1,animations)
        quick(array,p.index+1,high,animations)


    }


}
export function quicksortalgorithm(array){
const animations=[];
quick(array,0,array.length-1,animations);
return animations;

}
export function heapsortalgorithm(array){
const animations=[];
heap(array,animations);
return animations;

}
function heap(array,animations){
    let n=array.length;
    for(let i=Math.floor(n/2)-1;i>-1;i--){
        heapify(array,n,i,animations);
    }
    for(let i=n-1;i>0;i--){
        animations.push([i,0])
        let temp=array[i];
        array[i]=array[0];
        array[0]=temp;
        heapify(array,i,0,animations);
    }



}
function heapify(array,n,i,animations){
let largest=i;
let l=2*i+1;
let r=2*i+2;
if(l<n && array[i]<array[l]){
    largest=l;
}
if(r<n && array[largest]<array[r]){
    largest=r;
}
if(largest!=i){
    animations.push([i,largest]);
    let temp=array[i];
    array[i]=array[largest];
    array[largest]=temp;
    heapify(array,n,largest,animations)

}
   

}